.basic-single-select-fac {
	margin: 30px auto !important;
	width: 160px !important;
}

.css-qc6sy-singleValue {
	color: rgba(255, 255, 255, 0.9) !important;
}

.custom-single-select-fac__option--is-focused {
	background-color: transparent !important;
	cursor: pointer !important;
}

.custom-single-select-fac__option--is-selected {
	background-color: transparent !important;
	cursor: pointer !important;
}

.custom-single-select-fac__menu {
	margin-top: 0px !important;
	border-radius: 12px !important;
	background: #545454 !important;
	color: #fff !important;
}

.custom-single-select-fac__indicators {
	cursor: pointer !important;
}
.custom-single-select-fac__indicator-separator {
	display: none !important;
}



.custom-single-select-fac__control {
	/* width: 100% !important; */
	height: 62px !important;
}

.custom-single-select-fac__control{
	width: 100% !important;
}

.custom-single-select-fac__input-container {
	height: 62px !important;
	margin: 0 !important;
	padding: 0 !important;
}

* {
	margin: 0;
	padding: 0;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
/* width */
::-webkit-scrollbar {
	width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
	background: #222;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #888;
	border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #555;
}

