header {
	position: fixed;
	display: flex;
	flex-direction: row;
	background-color: #151515;
	/* border: 1px solid; */
	height: 90px;
	/* width: 100%; */
	width: 1920px;
	z-index: 100;
	top: 0;
}
.logo__header {
	margin-left: 104px;
	margin-top: 33px;
	cursor: pointer;
}
.ul__header_li {
	cursor: pointer;
	font-family: arial;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 18px;
	color: white;
	list-style: none;
	float: left;
	transition: 0.5s;
}
.ul__header_li:hover {
	cursor: pointer;
	font-family: arial;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 18px;
	color: white;
	list-style: none;
	float: left;
	color: #ad00ff;
}
.ul__header {
	width: 632px;
	margin-top: 39px;
	margin-left: 71px;
	display: flex;
	justify-content: space-between;
}
.vectorli__active {
	color: #ad00ff;
}
@keyframes vector__header {
	from {
		transform: rotate(-90deg);
	}
	to {
		transform: rotate(0deg);
	}
}
.vector__header {
	width: 10px;
	height: 6px;
	margin-left: 5px;
	transform: rotate(-90deg);
}
.vector__header_rot {
	transform: rotate(0deg);
	animation: vector__header 0.7s;
}
.button__header {
	border-radius: 12px;
	border: none;
	width: 124px;
	height: 52px;
	margin-top: 22px;
	color: white;
	font-family: Arial, Helvetica, sans-serif;
}
#btn1__header {
	background-color: #747474;
	margin-left: 530px;
	transition: 0.5s;
}
#btn1__header:hover {
	background-color: #6c33a3;
	cursor: pointer;
}
#btn2__header {
	background-color: #6c33a3;
	margin-left: 20px;
	transition: 0.5s;
}
#btn2__header:hover {
