.Akoshka {
	position: absolute;
	width: 206px;
	height: 134px;
	margin-left: 292px;
	background: rgba(50, 50, 50, 0.6);
	color: white;
	display: none;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	backdrop-filter: blur(8px);
	-webkit-backdrop-filter: blur(8px);
	border-bottom-left-radius: 12px;
	border-bottom-right-radius: 12px;
}

.Akoshka__on {
	position: fixed;
	animation: okno 0.7s;
	display: block;
	margin-top: 90px;
	z-index: 100;
}
.Akoshka ul {
	margin-top: 24px;
}
.Akoshka li {
	font-family: Arial;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	color: white;
	margin-top: 16px;
	list-style: none;
	text-align: center;
	transition: 0.5s;
	animation: text 0.7s;
}
.Akoshka li:hover {
	cursor: pointer;
	color: #ad00ff;
}
.akoshka__firstli {
	margin-top: 0;
}

