/* Sec1 */
.carousel-items {
	width: 400px;
	height: 350px;
	background: #ffffff;
	border-radius: 12px;
	margin-left: 24px;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;
	position: relative;
}
.carousel-items-images-wrapper {
	margin-top: 46px;
}
#first-item {
	margin-left: 0;
}
.carousel-item-number {
	font-family: Arial, Helvetica, sans-serif;
	font-weight: 700;
	font-size: 42px;
	line-height: 48px;
	font-style: normal;
	position: absolute;
	left: 8px;
	top: 8px;
	color: black;
}

.carousel-items-title {
	font-family: Arial, Helvetica, sans-serif;
	font-weight: 700;
	font-size: 26px;
	margin-top: 40px;
	color: black;

}

.carousel-item-text {
	font-weight: 400;
	font-size: 16px;
